.nav-item {
  padding-left: 5px;
  padding-right: 5px;
  padding-top: 0px;
  padding-bottom: 0px;
}

.nav-bar-cont {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 0px;
}

.dev-album-or-song-art-cont {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.dev-album-or-song-art-item {
  padding: 15px;
  display: flex;
  justify-content: center;
  align-items: vertical;
  flex-direction: column;
}
